/* Date Picker Default Styles */
.cs-datepicker {
  padding: 0;
  margin: 0;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  background-color: #fff;
  border: 1px solid #dfdfdf;
  border-top: none;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.075);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.075);
  min-width: 17em;
  width: auto;
  z-index: 1000 !important; }

.cs-datepicker * {
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0; }

.cs-datepicker table {
  font-size: 13px;
  margin: 0;
  border: none;
  border-collapse: collapse; }

.cs-datepicker .ui-widget-header,
.cs-datepicker .ui-datepicker-header {
  background-image: none;
  border: none;
  color: #fff;
  font-weight: normal;
  padding: .2em 0; }

.cs-datepicker .ui-datepicker-header .ui-state-hover {
  background: transparent;
  border-color: transparent;
  cursor: pointer; }

.cs-datepicker .ui-datepicker-title {
  margin: 0;
  padding: 10px 0;
  color: #fff;
  font-size: 14px;
  line-height: 14px;
  text-align: center; }

.cs-datepicker .ui-datepicker-prev,
.cs-datepicker .ui-datepicker-next {
  position: relative;
  top: 0;
  height: 34px;
  width: 34px; }

.cs-datepicker .ui-state-hover.ui-datepicker-prev,
.cs-datepicker .ui-state-hover.ui-datepicker-next {
  border: none; }

.cs-datepicker .ui-datepicker-prev,
.cs-datepicker .ui-datepicker-prev-hover {
  left: 0; }

.cs-datepicker .ui-datepicker-next,
.cs-datepicker .ui-datepicker-next-hover {
  right: 0; }

.cs-datepicker .ui-datepicker-next span,
.cs-datepicker .ui-datepicker-prev span {
  display: none; }

.cs-datepicker .ui-datepicker-prev {
  float: left; }

.cs-datepicker .ui-datepicker-next {
  float: right; }

.cs-datepicker .ui-datepicker-prev:before,
.cs-datepicker .ui-datepicker-next:before {
  font: normal 20px/34px 'dashicons';
  padding-left: 7px;
  color: #fff;
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 34px;
  height: 34px; }

.cs-datepicker .ui-datepicker-prev:before {
  content: '\f341'; }

.cs-datepicker .ui-datepicker-next:before {
  content: '\f345'; }

.cs-datepicker .ui-datepicker-prev-hover:before,
.cs-datepicker .ui-datepicker-next-hover:before {
  opacity: 0.7; }

.cs-datepicker select.ui-datepicker-month,
.cs-datepicker select.ui-datepicker-year {
  width: 33%; }

.cs-datepicker thead {
  color: #fff;
  font-weight: 600; }

.cs-datepicker th {
  padding: 10px; }

.cs-datepicker td {
  padding: 0;
  border: 1px solid #f4f4f4; }

.cs-datepicker td.ui-datepicker-other-month {
  border: transparent; }

.cs-datepicker tr:first-of-type td {
  border-top: 1px solid #f0f0f0; }

.cs-datepicker td.ui-datepicker-week-end {
  background-color: #f4f4f4;
  border: 1px solid #f0f0f0; }

.cs-datepicker td.ui-datepicker-today {
  background-color: #f0f0c0; }

.cs-datepicker td.ui-datepicker-current-day {
  background: #bbdd88; }

.cs-datepicker td .ui-state-default {
  background: transparent;
  border: none;
  text-align: center;
  text-decoration: none;
  width: auto;
  display: block;
  padding: 5px 10px;
  font-weight: normal;
  color: #444; }

.cs-datepicker td.ui-state-disabled .ui-state-default {
  opacity: 0.5; }

/* Default Color Scheme */
.cs-datepicker .ui-widget-header,
.cs-datepicker .ui-datepicker-header {
  background: #00a0d2; }

.cs-datepicker thead {
  background: #32373c; }

.cs-datepicker td .ui-state-hover {
  background: #0073aa;
  color: #fff; }

